import {queryCust} from '@/common/api.js'

export default {
	onLoad(options) {
		
		console.log(JSON.stringify(options))
		if(options.grpCode)
			this.custInfo.grpCode=options.grpCode;
		if(options.custCode)
			this.custInfo.custCode=options.custCode;	
			
	},
	onShow() {
		queryCust(this.custInfo.custCode,this.custInfo.grpCode).then(res=>{
			this.custInfo=res.custInfoList[0];
		})
	},
	filters:{
		phone(value){
			return value?value:'未绑定'
		}
	},
	data() {
		return {
			custInfo:{}
		};
	},
	methods:{
		toVerifyPhone(){
			if(this.custInfo.mobile){
				this.COMMONFUNCTION.toPage('/pages/verifyPhone/verifyPhone?phone='+this.custInfo.mobile+'&custCode='+this.custInfo.custCode);
			}else{
				this.COMMONFUNCTION.toPage('/pages/verifyPhone/verifyPhone?custCode='+this.custInfo.custCode);					
			}
		},
		toMyInfo(){
			this.COMMONFUNCTION.saveStorage('selectCustInfo',this.custInfo).then(res=>{
				this.COMMONFUNCTION.toPage('/pages/mycards/myInfo')
			})
		}
	}
}
